<template>
	<v-form v-model="valid" ref="form">
		<v-list dense>
			<v-list-item
				v-for="(item, i) in data.option" :key="i">
				<v-list-item-icon>
					<v-icon>mdi-radiobox-blank</v-icon>
				</v-list-item-icon>
				<v-list-item-content>
					<v-text-field
						dense
						placeholder="Option"
						:value="item"
						v-model="editOption[i]"
						required
						:rules="requiredRule"
						append-outer-icon="mdi-close"
						@click:append-outer="remove(i)"
						autocomplete="off"
					></v-text-field>
				</v-list-item-content>
			</v-list-item>

			<v-list-item>
				<v-list-item-icon>
					<v-icon>mdi-radiobox-blank</v-icon>
				</v-list-item-icon>
				<v-list-item-content class="add-option">
					<v-btn
						outlined
						:disabled="!valid"
						type="submit"
						@click.prevent="validate"
						small
						width="100"
						color="primary">
						add option
					</v-btn>
				</v-list-item-content>
			</v-list-item>
		</v-list>
	</v-form>
</template>

<style scoped>
	.add-option{ flex: none; }
</style>

<script>
	export default{
		props: ['data', 'index'],

		data: () => ({
			editOption: [],
			valid: true,
			requiredRule: [v=>!!v || 'Harus di isi']
		}),

		created() {
			this.editOption = this.data.option
		},

		methods: {
			validate() {
				if (this.$refs.form.validate()) {
					this.$emit('option', {
						index: parseInt(this.index),
						option: this.editOption
					})
					this.editOption.push('')
				}
			},
			remove(val) {
				this.editOption.splice(val, 1)
				this.$emit('option', {
					index: parseInt(val),
					option: this.editOption
				})
			}
		}
	}
</script>
