<template>
	<v-col cols="12" sm="6" class="pa-0">
		<span class="black--text">Jenis file yang diperbolehkan</span>
		<v-checkbox 
			v-model="files"
			label="Document"
			value="document"
		></v-checkbox>
		<v-checkbox 
			class="mt-0"
			v-model="files"
			label="Spreedsheet"
			value="spreedsheet"
		></v-checkbox>
		<v-checkbox 
			class="mt-0"
			v-model="files"
			label="PDF"
			value="pdf"
		></v-checkbox>
		<v-checkbox 
			class="mt-0"
			v-model="files"
			label="Image"
			value="image"
		></v-checkbox>
		<span class="black--text">Maksimal ukuran file</span>
		<v-select
			dense
			outlined
			:items="items"
			item-text="text"
			item-value="value"
			v-model="size"			
		></v-select>
	</v-col>
</template>


<script>
	export default{
		props: ['index'],

		data: () => ({
			files: [],
			items: [
				{ text: '500 KB', value: 500 },
				{ text: '1 MB', value: 1000 },
				{ text: '5 MB', value: 5000 },
				{ text: '10 MB', value: 10000 }
			],
			size: 0
		}),

		watch: {
			files() {
				this.$emit('acceptedFile', {
					index: parseInt(this.index),
					files: [...this.files]
				})
			},
			size(val) {
				this.$emit('maxSize', {
					index: parseInt(this.index),
					size: val
				})
			}
		}
	}
</script>