<template>
	<div>
		<div style="width: 100%;">
			<v-card class="mb-6">
				<v-card-title class="pb-0">
					<v-text-field
						filled
						placeholder="Judul traction"
						v-model="dataTraction.judul_traction"
						dense
						required
						:rules="requiredRule"
						autocomplete="off"
					></v-text-field>
				</v-card-title>
				<v-card-text>
					<v-textarea
						placeholder="Deskripsi"
						v-model="dataTraction.description"
						rows="2"						
						dense
						required
						:rules="requiredRule"
						autocomplete="off"
					></v-textarea>
				</v-card-text>
			</v-card>
			<v-alert 
				type="info"
				v-if="field.length === 0">
				Silahkan klik tombol tambah dibawah kanan
			</v-alert>
			<v-form v-model="valid" ref="formField">
				<v-card 
					class="mb-6" 
					v-for="(item, i) in field" :key="i">
					<v-card-title class="pt-3 pb-0">
						<v-text-field
							filled
							placeholder="Pertanyaan"
							v-model="dataTraction.pertanyaan[i].pertanyaan"
							dense
							required
							:rules="requiredRule"
							autocomplete="off"
						></v-text-field>
						<!-- <v-spacer></v-spacer>
						<v-switch
							v-model="item.required"
							:label="`Required`"
							color="purple"
							class="mt-0"
						></v-switch> -->
					</v-card-title>
					<v-card-text>
						<component 
							:is="item.type" 
							:index="i"
							:data="item"
							@option="editOption"
							@acceptedFile="acceptedFile"
							@maxSize="maxSize"
						></component>
					</v-card-text>
					<v-divider></v-divider>
					<v-card-actions>
						<v-spacer></v-spacer>
						<!-- <v-btn 
							icon @click="duplicate(i)"
							color="primary"
							large>
							<v-icon>mdi-content-copy</v-icon>
						</v-btn> -->
						<v-btn 
							icon @click="remove(i)"
							color="red"
							large>
							<v-icon>mdi-trash-can</v-icon>
						</v-btn>
					</v-card-actions>
				</v-card>

				<Notif :msg="msg" :status="status" @visible="visible" />

				<div class="d-flex mb-6">
					<v-spacer></v-spacer>
					<v-btn
						v-if="field.length > 0"
						depressed
						:loading="isLoading"
						:disabled="!valid"
						type="submit"
						@click.prevent="validate"
						color="primary">
						simpan
					</v-btn>
				</div>
			</v-form>
		</div>

		<Navigation @type="addField" />    
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import Notif from '@/components/Notif'
	import Navigation from '@/components/Incubator/Traction/Navigation'
	import Short from '@/components/Incubator/Traction/Question/Short'
	import Paragraph from '@/components/Incubator/Traction/Question/Paragraph'
	import MultipleChoice from '@/components/Incubator/Traction/Question/MultipleChoice'
	import MultipleValue from '@/components/Incubator/Traction/Question/MultipleValue'
	import Dropdown from '@/components/Incubator/Traction/Question/Dropdown'
	import FileUpload from '@/components/Incubator/Traction/Question/FileUpload'
	import Date from '@/components/Incubator/Traction/Question/Date'
	import Time from '@/components/Incubator/Traction/Question/Time'

	export default{
		components: {
			Time,
			Date,
			FileUpload,
			Dropdown,
			MultipleValue,
			MultipleChoice,
			Paragraph,
			Short,
			Navigation,
			Notif
		},

		data: () => ({
			dataTraction: {
				judul_traction: '',
				description: '',
				pertanyaan: []
			},
			field: [],
			title: '',
			required: false,
			valid: true,
			requiredRule: [v => !!v || 'Harus di isi'],
			msg: {
				success: 'Data berhasil ditambahkan',
				error: 'Data gagal ditambahkan',
				visible: false
			}
		}),

		computed: {
			...mapState('dataIncubatorTraction', ['isLoading', 'status'])
		},

		methods: {
			addField(val) {
				this.field.push({
					type: val,
					title: '',
					option: ['Option 1'],
					required: false,
					accept_file: [],
					max_size: 0
				})
				this.dataTraction.pertanyaan.push({ pertanyaan: '' })
			},
			remove(val) {
				this.field.splice(val, 1)
			},
			duplicate(val) {
				this.field.push(this.field[val])
			},
			editOption(val) {
				this.field[val.index].option = val.option 
			},
			acceptedFile(val) {
				this.field[val.index].accept_file = val.files
			},
			maxSize(val) {
				this.field[val.index].max_size = val.size
			},
			validate() {
				if (this.$refs.formField.validate()) {
					// console.log(this.field)
					this.$store.dispatch('dataIncubatorTraction/postDataTraction', this.dataTraction)
					this.$store.dispatch('dataIncubatorTraction/updateIsLoading', true)
					this.msg.visible = true
				}
			},
			visible(val) {
				this.msg.visible = val
				this.$store.commit('dataIncubatorTraction/updateStatus', null)
			}
		}
	}
</script>
