<template>
  <div>
    <v-btn
      fixed
      bottom
      right
      color="purple"
      dark
      @click="tambah('Short')"
      fab>
      <v-icon>mdi-plus</v-icon>
    </v-btn>
    <v-navigation-drawer 
      v-model="drawer" 
      temporary
      :permanent="false"   
      right
      fixed> 
      <div class="py-2 pl-3 title">Add Questions</div>
      <v-list
        dense
        nav>
        <v-list-item-group v-model="list" color="purple">
          <v-list-item
            v-for="item in items"
            :key="item.title"
            link @click="tambah(item.type)">
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>   
        </v-list-item-group>  
      </v-list>         
    </v-navigation-drawer>
  </div>  
</template>

<script>
	export default {
		data: () => ({
      drawer: false,
			list: 0,
			items: [
        { title: 'Short Answer', icon: 'mdi-text-short', type: 'Short' },
        { title: 'Paragraph', icon: 'mdi-text', type: 'Paragraph' },
        { title: 'Multiple Choice', icon: 'mdi-radiobox-marked', type: 'MultipleChoice' },
        { title: 'Multiple Value', icon: 'mdi-checkbox-marked', type: 'MultipleValue' },
        { title: 'Dropdown', icon: 'mdi-arrow-down-drop-circle', type: 'Dropdown' },
        { title: 'File Upload', icon: 'mdi-cloud-upload', type: 'FileUpload' },
        { title: 'Date', icon: 'mdi-calendar', type: 'Date' },
        { title: 'Time', icon: 'mdi-clock', type: 'Time' }
      ]
		}),

    methods: {
      tambah(val) {
        this.$emit('type', val)
      }
    }
	}
</script>